















import Vue from "vue";
export default Vue.extend({
  name: "About",
  data() {
    return {
      devMode: false
    };
  },
  created() {
    // Boolean変換忘れないで
    this.devMode = this.toBoolean(this.getSetting("bibouroku_list_cache", false));
  },
  methods: {
    // 設定保存用関数
    saveSetting(name: string, value: any) {
      localStorage.setItem(name, value);
    },
    // 設定取得変数。
    getSetting(name: string, error: any) {
      const value = localStorage.getItem(name);
      if (value === null) {
        return error;
      } else {
        return value;
      }
    },
    // String to Boolean
    toBoolean(data: any) {
      return data === "true";
    },
    openMastodon(){
      window.open('https://best-friends.chat/@takusan_23')
    },
    openRepo(){
      window.open('https://github.com/takusan23')
    }
  },
  watch: {
    // 変数監視？
    devMode(value) {
      this.saveSetting("bibouroku_list_cache", value);
    }
  }
});
